import {Redirect, Route} from 'react-router-dom';
import React, {ReactNode} from 'react';
import MobileDetect from './MobileDetect';

export interface ComponentProps<T> {
    initial?: T,
}

export interface ComponentModelState<T> {
    payload: T,
    isLoading: boolean,
    isRedirecting: boolean,
    isRouting: boolean,
    redirectTo: string,
    routeTo?: (props: any) => JSX.Element,
    errors: any[];
}

export class ViewModel<T> extends React.Component<any, ComponentModelState<T>> {

    protected get payload(): T {
        return this.state.payload;
    }

    protected set payload(val) {
        this.setState(s => {
            return {...s, payload: val}
        })
    }
    
    protected get isMobile() {
        return MobileDetect.Any();
    }

    public constructor(props: any, initialState?: ComponentProps<T>) {
        super(props);

        let hasInitialState = initialState?.initial;
        let payload = hasInitialState ? Object.assign({}, initialState?.initial) as T : {} as T

        this.state = {
            payload: payload,
            isLoading: false,
            isRedirecting: false,
            isRouting: false,
            redirectTo: '/',
            routeTo: undefined,
            errors: []
        };

        this.handleChange = this.handleChange.bind(this);
    }

    handleChange(event: any) {
        const target = event.target;
        const value = target.type === 'checkbox' ? target.checked : target.value;
        const name = target.name;

        this.setState(s => {
            let model: any = {...s.payload};
            model[name] = value;
            return {...s, payload: model};
        });
    }

    async loading(state: boolean = true) {
        this.setState({isLoading: state});
    }

    hasError(key: string): boolean {
        let found = false;
        
        this.state.errors.forEach(x => {
            if (x.key === key.toLowerCase()) found = true;
        });
        
        return found;
    }

    getError(key: string, defaultValue: string): string {
        let found = defaultValue;

        this.state?.errors.forEach(x => {
            if (x.key === key.toLowerCase()) found = x.value;
        });

        return found;
    }

    async setError(key: string, value: string) {
        this.setState(s =>{
           let e = s.errors;
           e.push({key: key.toLowerCase(), value: value});
           return {...s, errors: e};
        });
    }
    
    hasErrors(): boolean {
        return this.state.errors.length > 0;
    }
    
    async clearErrors(){
        this.setState(s =>{
            return {...s, errors: []};
        });
    }

    async redirectTo(uri: any) {
        this.setState({isRedirecting: true, redirectTo: uri});
    }

    async routeTo(comp: (props: any) => JSX.Element) {
        this.setState({isRouting: true, routeTo: comp});
    }

    public notify(): ReactNode {
        return undefined
    }

    public render(): ReactNode {
        if (this.state.isRedirecting) {
            return <Redirect to={this.state.redirectTo}/>;
        } else if (this.state.isRouting) {
            return <Route component={this.state.routeTo}/>
        } else {
            return this.notify()
        }
    }
}