import {CSSProperties} from 'react';
import UserCreateViewModel from '../viewModels/UserCreateViewModel';
import React from 'react';
import {
    Button,
    TextField,
    Typography
} from '@material-ui/core';
import Summary from '../../../application/components/Summary';


export default class UserCreate extends UserCreateViewModel {

    css = {

        form: {
            margin: 0,
            padding: 0,
            display: 'grid',
            alignContent: 'center',
            alignItems: 'center',
            gridTemplateRows: '1fr 25%',
            maxWidth: this.isMobile ? '100%' : '60%',
        } as CSSProperties,
        title: {
            fontFamily: 'Comfortaa',
            fontWeight: 'bold',
            color: '#0288D1',
        } as CSSProperties,
        subtitle: {
            color: '#03A9F4'
        } as CSSProperties,
        input: {
            marginTop: '0px',
            width: '100%'
        } as CSSProperties,

        button: {
            minWidth: '100%',
            margin: '12px 0',
            height: 50,
        } as CSSProperties,

    }


    notify(): React.ReactNode {
        return (
            <form key={'user-create'} style={this.css.form} autoComplete='off' aria-disabled={this.state.isLoading}>

                <div>
                    <Typography style={this.css.title} variant='h6' component='div'>
                        CADASTRAR MINHA CONTA
                    </Typography>
                    <Typography style={this.css.subtitle} variant='button' component='div'>
                        Informe seus dados abaixo
                    </Typography>

                    <Summary items={this.state.errors} onClose={() => this.clearErrors()}/>

                    <div>
                        <TextField style={this.css.input} type='text' name='name' label='Nome'
                                   onChange={(e) => this.handleChange(e)}
                                   helperText='nome completo'
                                   error={this.hasError('name')}/>

                        <TextField style={this.css.input} type='email' name='email' label='e-mail'
                                   onChange={(e) => this.handleChange(e)}
                                   helperText='e-mail válido para confirmar sua conta'
                                   error={this.hasError('email')}/>

                        <TextField style={this.css.input} type='email' name='confirmEmail'
                                   label='Confirmação de e-mail'
                                   onChange={(e) => this.handleChange(e)}
                                   helperText='confirme o e-mail informado anteriormente'
                                   error={this.hasError('confirmEmail')}/>

                        <TextField style={this.css.input} type='password' name='password' label='Senha'
                                   onChange={(e) => this.handleChange(e)}
                                   helperText='deve conter entre 6 e 8 dígitos'
                                   error={this.hasError('password')}/>

                        <TextField style={this.css.input} type='password' name='confirmPassword'
                                   label='Confirmação de senha'
                                   onChange={(e) => this.handleChange(e)}
                                   helperText='confirme a senha digitada anteriormente'
                                   error={this.hasError('confirmPassword')}/>
                    </div>
                </div>

                <div>

                    <Button style={this.css.button} variant='contained' color='primary'
                            onClick={() => this.save()}>
                        Salvar
                    </Button>
                    <Button style={this.css.button} variant='text' color={'primary'}
                            onClick={() => this.props.switch()}>
                        JÁ SOU CADASTRADO
                    </Button>
                </div>

            </form>
        );
    }
}