import React, {Component, CSSProperties} from 'react';
import {
    Button,
    createMuiTheme,
    Divider,
    IconButton,
    List,
    ListItem,
    ListItemIcon,
    ListItemText, ThemeProvider,
    Typography
} from '@material-ui/core';
import CloseIcon from '@material-ui/icons/Close';
import Themes from '../Themes';


export default class Summary extends Component<any, any> {
    css = {
        root: {
            backgroundColor: Themes.error.palette.secondary.light,
            fontSize: '10pt',
            paddingTop: 0
        } as CSSProperties,
        header: {
            backgroundColor: Themes.error.palette.primary.main,
            color: Themes.error.palette.primary.contrastText,
            fontSize: '10pt',
            padding: '0px 6px',
        } as CSSProperties,
        icon: {
            display: 'grid',
            justifyItems: 'end'
        } as CSSProperties,
        item: {
            padding: '0px 8px',
        } as CSSProperties,
    }


    render() {
        let items = this.props.items.filter((item: any) => item.key === 'none');

        if (items <= 0)
            return <div/>;
        else
            return (
                <List style={this.css.root} >
                    <ListItem style={this.css.header} dense>
                        <ListItemText>
                            <Typography variant={'button'} >
                                {this.props?.title ?? 'ATENÇÃO'}
                            </Typography>
                        </ListItemText>
                        <ListItemIcon style={this.css.icon}>
                            <IconButton onClick={() => this.props.onClose()} size='small' >
                                <CloseIcon/>
                            </IconButton>
                        </ListItemIcon>
                    </ListItem>
                    <Divider/>
                    {
                        items.map((item: any, index: number) => {
                            return this.renderItem(item, index)
                        })
                    }
                </List>
            );

    }

    private renderItem(item: any, index: number) {
        return <ListItem key={`summary-item-${index}`} style={this.css.item}>
            <ListItemText secondary={item.value}/>
        </ListItem>

    }
}