import {ViewModel} from '../../../application/ViewModel';
import Users, {UserCreateModel} from '../models/UserModels';


export default class UserCreateViewModel extends ViewModel<UserCreateModel> {
    
    handleChange(event: any) {
        super.handleChange(event);
        
        if (this.hasErrors())
            this.validate();
    }

    async validate() {
        await this.clearErrors();

        if (this.payload.name === '' || this.payload.name === undefined)
            await this.setError('name', 'campo obrigatório');

        if (this.payload.email === '' || this.payload.email === undefined)
            await this.setError('email', 'campo obrigatório');

        if (this.payload.confirmEmail === '' || this.payload.confirmEmail === undefined)
            await this.setError('confirmEmail', 'campo obrigatório');

        if (this.payload.password === '' || this.payload.password === undefined)
            await this.setError('password', 'campo obrigatório');

        if (this.payload.confirmPassword === '' || this.payload.confirmPassword === undefined)
            await this.setError('confirmPassword', 'campo obrigatório');
    }

    async save() {
        await this.validate();
        if (!this.hasErrors()) {
            await this.loading();
            let payload = await Users.parseToNewUserCommand(this.payload);

            Users.register(payload)
                .then((result) => {
                    this.redirectTo('/');
                })
                .catch((error) => {
                    error.response?.data?.errors.map((x: any) => {
                        console.log(x);
                        this.setError(x.property, x.message);
                    });
                })
                .finally(() => this.loading(false));
        }
    }
}