import {api} from '../../../application/Api';
import SigninViewModel from '../viewModels/SigninViewModel';

export default class Users {
    static register = async (payload: NewUser) =>
        api.post('identity/user', payload)
    
    static login = async (payload: SigninModel) =>
        api.post('access/login', payload)
    
    static async parseToNewUserCommand(model: UserCreateModel): Promise<NewUser>{
        return {
            name: model.name,
            email: model.email,
            confirmEmail: model.confirmEmail,
            confirmPassword: model.confirmPassword,
            password: model.confirmPassword,
            phone: '***',
            username: model.username
        }        
    }
}

export interface SigninModel {
    username: string;
    password: string;
}

export interface NewUser extends SigninModel{
    name: string;
    email: string;
    phone: string;
    confirmEmail: string;
    confirmPassword: string;
}

export interface UserCreateModel extends NewUser{
    errors?: any;
}

export interface LoginModel{
    isSigninForm: boolean
}

